* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  html {
    font-family: "Montserrat", sans-serif;
    height: 100vh;
    overflow: hidden;
  }
  body {
    background-color: #2b4162;
    background-image: linear-gradient(315deg, #2b4162 0%, #12100e 74%);
    font-family: 'Space Grotesk', sans-serif;
      font-weight: 300;
  }
  
  header {
    margin: 5rem;
  }
  
  .overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 10vh;

}

.text {
	font-size: 2rem;
	display: flex;
	position: absolute;
	top: 2vh;
	left: 50%;
	transform: translateX(-50%);
	user-select: none;
  color: #ffffff;

	.wrapper {
		padding: 0.5rem;
		.letter {
      padding-bottom: 0.5rem;
			transition: ease-out 1s;
			transform: translateY(40%);
		}
		.shadow {
			transform: scale(1, -1);
			color: #5e5e5e;
			transition: ease-in 5s, ease-out 5s;
      font-size: 36px;
  background-image: linear-gradient(to bottom, rgba(255, 0, 0, 0), #2b2a2a); /* Define the gradient */
  -webkit-background-clip: text; /* Clip the background to the text */
  background-clip: text; /* Clip the background to the text */
  color: transparent; /* Make the text transparent */
  -webkit-text-fill-color: transparent; /* Hide the text */
		}
		&:hover {
			.letter {
				transform: translateY(-200%);
			}
			.shadow {
				opacity: 0;
				transform: translateY(200%);
			}
		}
	}
}

  
  footer {
    position: absolute;
    text-align: center;
    bottom: 0;
    width: 100%;
    height: 1rem;
    background-color: transparent;
  }
  
  footer p {
    color: #ccc;
    font-size: xx-small;
  }
  .home{
    display: flex;
    gap:10rem;
    justify-content: center;
    margin-top: 10rem;
    margin-bottom: 5rem;
  }
  
  .groups{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    border-radius: 70%;
    height: .8vh;
    margin-top: 2rem;
  }

  .g{
    background: #ffffff;
    box-shadow: 0 2px 5px #170d0d;
    align-items: center;
    text-align: center;
    padding: 0.75rem;
    height: 3rem;
    width: fit-content;
    margin: 10rem;
  }
  .group {

    background: #313131;
    box-shadow: 0 2px 5px #170d0d;
    align-items: center;
    text-align: center;
    padding: 0.75rem;
    height: 3rem;
    width: 10rem;
    margin: 0.3rem;
  }

  .group:hover{
    background-color: #a1a1a1;
  }

  .group h1 {
    font-size: 1.1em;
    margin-bottom: 6px;
    color: #ffffff;
  }
  .login {
    border-radius: 10%;
    box-shadow: 0 2px 5px #170d0d;
    display: flex;
    gap: 1.5rem;
    flex-direction: column;
    background-color: #0f0f0f;
    color: #ffffff;
    width: 25%;
    height: 50vh;
    margin: auto;    
    margin-top: 13rem;
    align-items: center;
    justify-content: center;
  }

  .login > input {
    width: 70%;
    height: 2rem;
    padding: 1rem;
    box-shadow: 0 2px 5px #170d0d;
    border: #f6f6f6;
    background-color: #fdeeed;
  }
.btn{
  background: linear-gradient(to bottom right, #EF4765, #FF9A5A);
  border: 0;
  border-radius: 12px;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system,system-ui,"Segoe UI",Roboto,Helvetica,Arial,sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 2.5;
  outline: transparent;
  padding: 0 1rem;
  text-align: center;
  text-decoration: none;
  transition: box-shadow .2s ease-in-out;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
}

.btn:not([disabled]):focus {
  box-shadow: 0 0 .25rem rgba(0, 0, 0, 0.5), -.125rem -.125rem 1rem rgba(239, 71, 101, 0.5), .125rem .125rem 1rem rgba(255, 154, 90, 0.5);
}

.btn:not([disabled]):hover {
  box-shadow: 0 0 .25rem rgba(0, 0, 0, 0.5), -.125rem -.125rem 1rem rgba(239, 71, 101, 0.5), .125rem .125rem 1rem rgba(255, 154, 90, 0.5);
}

.exercisegroup{
  height: 65vh;
  background-color:transparent;
  margin: auto;
  margin-top: 0;
  display: flex;
}

.exercisetabs{
  flex:1;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 2.5rem;
  height: 100%;
}

.exerciselist{
  flex:3;
  display: flex;
  flex-direction: column;
  background-color: #a1a1a1;
  width: 10rem;

}
.exercisetab{
  background-color: #414141;
  writing-mode: vertical-rl;
  transform: scaleX(-1) scaleY(-1);
  border-top-right-radius: 10%; 
  border-bottom-right-radius: 10%;
  flex: 1;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  border:#a1a1a1;
  ;
}
.activetab{
  background-color: #a1a1a1;
  writing-mode: vertical-rl;
  transform: scaleX(-1) scaleY(-1);
  border-top-right-radius: 10%; 
  border-bottom-right-radius: 10%;
  flex: 1;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
}
.exercises{
  flex:10;
  background-color: #d0d0d0;
  border-bottom-right-radius: 20px;
  display:grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  padding: 2rem;
  overflow: auto;
}

.workout{
  display: flex;
  flex-direction: column;
  width: 15rem;
  height: 17rem;
  margin: auto;
  background: #e0e0e0;
  box-shadow: 20px 20px 60px #bebebe,
               -20px -20px 60px #ffffff;
}
.exercise-img{
  width: 15rem;
  height: 10rem;
  display: flex;
}
.exercise-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.exercise-info{
  margin: 1rem;
}
.tracking{
  display: flex;
  width: 100%;
  justify-content:space-between ;
}
.tracking button{
  border-radius: 50%;
  border: none;
  width: 1rem;
  height: 1rem;
  align-items: center;
  background-color: #b2b2b2;
  color: #d4d4d4;
  box-shadow: rgba(0, 0, 0, 0.04) 0px 3px 5px;
}
.update{
  display: flex;
  gap: 1rem;
}


#exercise-name{
  margin-bottom: 1rem;
}

.bodysection{
  flex: 1;
  color: #f9f9f9;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bodysection:hover{
  color: #FFFFFF;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.exercisetab:hover{
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.hometab{
  width: 10rem;
  height: 3rem;
  line-height: 3rem;
  margin: auto;  
  margin-right: 0px;
  background-color: #d0d0d0;
  color: #0f0f0f;
  text-align: center;
  border-top-right-radius:20px;
  border-top-left-radius: 20px;
}
.exercisepage{
  flex-direction: column;
  width: 70%;
  margin: 10rem;
  margin-top: 7rem;
}

/* Calendar css */

/* Set the background and text color for the calendar */
.fc {
  background-color: transparent; /* Calendar background */
  color: black; /* Default text color */
}

/* Toolbar (header) customization */
.fc-toolbar {
  background-color: transparent; 
  color: rgb(255, 255, 255); /* Text color */
}

/* Day cell customization */
.fc-day {
  background-color: white; /* Background color for day cells */
  color: black; /* Text color for day cells */
}

/* Event customization */
.fc-event {
  background-color: #454545; /* Background for events */
  color: white; /* White text for events */
  border: none; /* Remove border if any */
}

/* Current day highlight */
.fc-day-today {
  background-color: #e9ecef; /* Background for today */
}

.fc .fc-daygrid-day.fc-day-today {
  background-color: rgb(164, 201, 249);
}

/* Selected day highlight */
.fc-day-selected {
  background-color: #b5d4f8; /* Light blue background for selected day */
}

/* Hover effect on events */
.fc-event:hover {
  background-color: #5a67a1; /* Darker blue on hover */
}

.fc .fc-toolbar-title {
  font-size: 1.5rem;
  margin-right: 1rem;
}